let pathnameTmp;

export default class CategoriesList extends Component {
	constructor() {
		super();
		this.state = {
			renderList: this.mapListHandle(Object.entries(foodCategories).map(e => e[0]))
		};
	}

	componentDidMount() {
		pathnameTmp = window.location.hash.split("#")[1];
		this.listIntroAnimation();
	}
	componentDidUpdate() {
		this.listIntroAnimation();
	}

	listIntroAnimation = async _ => {
		S("#categoriesListBody")
			.css("position", "fixed")
			.css("left", "0%")
			.css("transform", "translate(-100%, 0)")
			.css("opacity", "0")
			.css("transition", "left 350ms, opacity 350ms");
		await wait(20);
		S("#categoriesListBody")
			.css("transform", "translate(-50%, 0)")
			.css("left", "50%")
			.css("opacity", "1");
		await wait(350);
		S("#categoriesListBody").removeAttr("style");
	};

	mapListHandle = arr =>
		arr.map(e => (
			<li key={Math.random() * 100} onClick={this.onClickListHandle}>
				{e}
			</li>
		));

	onClickListHandle = e => {
		if (foodCategories[e.target.innerHTML]) {
			this.setState({ renderList: this.mapListHandle(foodCategories[e.target.innerHTML]) });
			S("#categoriesListPageHeaderTitle").text(e.target.innerHTML);
			window.history.pushState(
				undefined,
				undefined,
				"/search" +
					window.location.hash +
					"#" +
					(Object.entries(foodCategories)
						.map(e => e[0])
						.indexOf(e.target.innerHTML) +
						1)
			);
		}
	};

	checkHash = _ => {
		let hash;
		switch (pathnameTmp) {
			case "breakfast":
				hash = "الفطور";
				break;
			case "lunch":
				hash = "الغداء";
				break;
			case "dinner":
				hash = "العشاء";
				break;
			case "snacks":
				hash = "وجبة خفيفة";
				break;
			default:
				break;
		}
		return hash;
	};

	render() {
		return (
			<div id='categoriesListPage'>
				<div id='categoriesListPageHeader'>
					<svg
						id='categoriesListPageBack'
						xmlns='http://www.w3.org/2000/svg'
						viewBox='0 0 24 24'
						onClick={_ => {
							if (window.location.hash.split("#")[3]) {
								this.setState({
									renderList: this.mapListHandle(Object.entries(foodCategories).map(e => e[0]))
								});
								S("#categoriesListPageHeaderTitle").text("الأصناف");
								window.history.pushState(undefined, undefined, "/search#" + pathnameTmp + "#categoriesList");
							} else {
								updateRenderState([<SearchPage key={Math.random() * 100} title={this.checkHash()} />]);
								window.history.pushState(undefined, undefined, "/search#" + pathnameTmp);
							}
						}}
					>
						<path d='M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z' />
					</svg>
					<p id='categoriesListPageHeaderTitle'>الأصناف</p>
				</div>
				<ul id='categoriesListBody'>{this.state.renderList}</ul>
			</div>
		);
	}
}
