import React, { Component, Fragment } from "react";
import "./index.css";
import { S, wait, ProgressBar } from "./alabsi";
import { gsap } from "gsap";
import Diary from "./diary";
import { updateRenderState } from ".";
import SearchPage from "./searchPage";

let isReversed = false;
let isPressable = true;
let tl;

class MainPage extends Component {
	constructor() {
		super();
		this.state = {
			carbPercent: "50%",
			proteinPercent: "50%",
			fatPercent: "50%",
			remainPercent: 75
		};
	}

	componentDidMount() {
		this.responsiveResizing();

		this.introPageTransition();

		window.addEventListener("resize", this.responsiveResizing);
		window.addEventListener("scroll", this.hideHeadreScrollAnimation);

		this.setRemainPercent();

		const getMainColor = S().getCssVar("--main-color");
		S("#navMainPageButton svg").css("fill", getMainColor);
		S("#navMainPageButton p").css("color", getMainColor);
	}

	componentWillUnmount() {
		window.removeEventListener("scroll", this.hideHeadreScrollAnimation);
		window.removeEventListener("resize", this.responsiveResizing);
		window.removeEventListener("resize", this.resizeProgresHandler);
		S("#navMainPageButton svg").removeAttr("style");
		S("#navMainPageButton p").removeAttr("style");
	}

	mountSearchComponent = (ar, en) => {
		updateRenderState(<SearchPage key={Math.random() * 100} title={ar} />);
		isReversed = false;
		window.history.pushState(undefined, undefined, "/search#" + en);
	};

	introPageTransition = _ => S(".MainPage").css("opacity", "1");

	hideHeadreScrollAnimation = _ => {
		const getHeaderHeight = S("#header").getStyle("height", true);
		let multiplier = (1 / (getHeaderHeight / 1.2)) * (getHeaderHeight / 1.2 - window.scrollY);
		multiplier = multiplier < 0 ? 0 : multiplier;
		S(".remainValues")
			.css("transform", `scale(${multiplier}) translate(0, -50%)`)
			.css("opacity", multiplier);
		S("#macrosPercent")
			.css("transform", `scale(${multiplier})`)
			.css("opacity", multiplier);
	};

	responsiveResizing = _ => {
		// for the header sticky postion from the top
		const getHeaderHeight = -S("#header").getStyle("height", true) + 55 + "px";
		S("#header").css("top", getHeaderHeight);

		// for add buttons postion from the left
		const getPageMargin = ((S(".MainPage").getStyle("margin-left", true) + 45) * 100) / window.innerWidth + "%";
		if (!isReversed) S(".floatButton").css("left", getPageMargin);

		// for macros percentage bars position from the bottom
		const getHeaderTitleHeight = S("#title").getStyle("height", true);
		const getHeaderPercentNumHeight = S(".percentNum").getStyle("height", true);
		const calcBottom = getHeaderTitleHeight + getHeaderPercentNumHeight + 38 + "px";
		S("#macrosPercent").css("bottom", calcBottom);
	};

	animatAddFooButton = async _ => {
		const animDuration = 0.1;
		if (!isReversed && isPressable) {
			isPressable = false;
			tl = gsap
				.timeline()
				.to("#addFoodBG", { duration: animDuration, opacity: 1, display: "block" })
				.to("#addButton", { duration: animDuration, rotate: 135 }, "f")
				.to(
					"#addSnacks",
					{
						duration: animDuration,
						display: "block",
						bottom: "calc(50% - 100px)",
						left: "50%",
						scale: 1,
						delay: 0.1
					},
					"f"
				)
				.to(
					"#addDinner",
					{
						duration: animDuration,
						display: "block",
						bottom: "50%",
						left: "calc(50% - 100px)",
						scale: 1,
						delay: 0.2
					},
					"f"
				)
				.to(
					"#addLaunch",
					{
						duration: animDuration,
						display: "block",
						bottom: "50%",
						left: "calc(50% + 100px)",
						scale: 1,
						delay: 0.3
					},
					"f"
				)
				.to(
					"#addBreakfast",
					{ duration: animDuration, display: "block", bottom: "calc(50% + 100px)", left: "50%", scale: 1 },
					"f"
				);
			isReversed = true;
			await wait(600);
			isPressable = true;
		} else if (isReversed && isPressable) {
			this.responsiveResizing();
			isPressable = false;
			tl.reverse();
			isReversed = false;
			await wait(600);
			isPressable = true;
		}
	};

	setRemainPercent = _ => {
		let getWidth = S("#header").getStyle("width", true) / 3 / 2;
		ProgressBar("#remainProgress", {
			input: this.state.remainPercent,
			R: getWidth,
			width: 8,
			fillColor: "none",
			backgroundColor: "rgb(250,250,245,0.3)",
			textColor: "white",
			gradColor1: "#FFFEFF",
			gradColor2: "#D7FFFE"
		});

		window.addEventListener("resize", this.resizeProgresHandler);
	};
	resizeProgresHandler = _ => {
		let getWidth = S("#header").getStyle("width", true) / 3 / 2;
		S("#remainProgress svg")
			.css("height", getWidth * 2 + 8)
			.css("width", getWidth * 2 + 8);
		S("#remainProgress svg circle")
			.css("r", getWidth)
			.attr("cx", getWidth + 4)
			.attr("cy", getWidth + 4);
		S("#remainProgress svg text")
			.css("font-size", getWidth / 2)
			.attr("dy", getWidth / 6);
		const calcProgress = 2 * Math.PI * getWidth - (this.state.remainPercent * (2 * Math.PI * getWidth)) / 100;
		S("#remainProgress svg circle:nth-child(3)")
			.css("stroke-dashoffset", calcProgress)
			.css("stroke-dasharray", 2 * Math.PI * getWidth);
	};
	render() {
		return (
			<Fragment>
				<div id='addFoodBG' onClick={this.animatAddFooButton}></div>
				<div className='MainPage'>
					<h2 id='title'>حاسبة الطعام</h2>
					<div id='header'>
						<svg width='100%' viewBox='0 0 320 300' fill='none'>
							<linearGradient id='Gradient1' gradientTransform='rotate(45)'>
								<stop offset='20%' />
								<stop offset='120%' />
							</linearGradient>
							<path
								d='M0 0H320V286.491C320 286.491 249 300 159 300C69 300 0 286.491 0 286.491V0Z'
								fill='url(#Gradient1)'
							/>
						</svg>
						<div className='remainValues'>
							<div className='caloriesEaten'>
								<p>750</p>
								<p>المحروقة</p>
							</div>
							<div id='remainProgress'></div>
							<div className='caloriesRemain'>
								<p>1500</p>
								<p>الـبـاقيـة</p>
							</div>
						</div>
						<div id='macrosPercent'>
							<div className='macrosPercentContainer'>
								<p className='macroName'>الكربوهيدرات</p>
								<div className='macrosPercentLine' style={{ width: this.state.carbPercent }}></div>
								<p className='percentNum'>
									50<span>%</span>
								</p>
							</div>
							<div className='macrosPercentContainer'>
								<p className='macroName'>البروتين</p>
								<div className='macrosPercentLine' style={{ width: this.state.proteinPercent }}></div>
								<p className='percentNum'>
									50<span>%</span>
								</p>
							</div>
							<div className='macrosPercentContainer'>
								<p className='macroName'>الدهون</p>
								<div className='macrosPercentLine' style={{ width: this.state.fatPercent }}></div>
								<p className='percentNum'>
									50<span>%</span>
								</p>
							</div>
						</div>
					</div>

					<Diary />

					<div id='addButton' className='circleButton' onClick={this.animatAddFooButton}>
						<svg width='24' height='24' viewBox='0 0 24 24'>
							<path d='M24 10h-10v-10h-4v10h-10v4h10v10h4v-10h10z' fill='white' />
						</svg>
					</div>
					<div
						id='addBreakfast'
						className='circleButton floatButton'
						onClick={_ => this.mountSearchComponent("الفطور", "breakfast")}
					>
						<img src='./assets/bread.png' alt='bread' />
						<p>الفطور</p>
					</div>
					<div
						id='addLaunch'
						className='circleButton floatButton'
						onClick={_ => this.mountSearchComponent("الغداء", "lunch")}
					>
						<img src='./assets/soup.png' alt='soup' />
						<p>الغداء</p>
					</div>
					<div
						id='addDinner'
						className='circleButton floatButton'
						onClick={_ => this.mountSearchComponent("العشاء", "dinner")}
					>
						<img src='./assets/fish.png' alt='fish' />
						<p>العشاء</p>
					</div>
					<div
						id='addSnacks'
						className='circleButton floatButton'
						onClick={_ => this.mountSearchComponent("وجبة خفيفة", "snacks")}
					>
						<img src='./assets/banana.png' alt='banana' />
						<p>وجبة خفيفة</p>
					</div>
				</div>
			</Fragment>
		);
	}
}
export default MainPage;
export { dateGetter };

let dateIndex = 1;
function dateGetter(decrement = false) {
	const days = ["الأحد", "الاثنين", "الثلاثاء", "الاربعاء", "الخميس", "الجمعة", "السبت"];
	const newDate = new Date();
	const newDecrementDate = new Date(
		newDate.getFullYear(),
		newDate.getMonth(),
		decrement ? newDate.getDate() - dateIndex : newDate.getDate() + dateIndex
	);
	const getYear = newDecrementDate.getFullYear();
	const getMonth = newDecrementDate.getMonth();
	const getDate = newDecrementDate.getDate();
	const getDay = newDecrementDate.getDay();
	decrement ? dateIndex-- : dateIndex++;
	return `${getYear} - ${getMonth} - ${getDate}  ${days[getDay]}`;
}

